<template>

    <v-container fluid >

        <v-layout fill-height
            :row='$vuetify.breakpoint.mdAndUp'
            :column='$vuetify.breakpoint.smAndDown'
            
            :align-start='$vuetify.breakpoint.smAndDown'
            
            :justify-start='$vuetify.breakpoint.smAndDown'

            v-bind='layoutAttr'

        
        >
            
        <!--   <h1 class='display-2 grey--text text--darken-1'>Categories</h1> -->

        <!-- Filter Panel -->
            <v-flex 
                xs12
                sm12
                md2   
                style='background: #E3E3E3; position: relative; width: 100% !important;' 
            >
                <v-container fluid>

                    <v-layout 
                        :column='$vuetify.breakpoint.mdAndUp'
                        :justify-center='$vuetify.breakpoint.mdAndUp'
                        :align-center='$vuetify.breakpoint.mdAndUp'
        
                        :row='$vuetify.breakpoint.smAndDown'
                        :align-start='$vuetify.breakpoint.smAndDown'

                        

                    >
                        <v-flex grow  class='' px-1 my-1>
                                <v-btn round depressed fab @click='filter = "all"'> All </v-btn> 
                        </v-flex>
                        <v-flex grow  class='' px-1 my-1> 
                            <v-layout column wrap align-center justify-center style='' @click='filter="fittings"' :class='{"filterButton" : true,"fActive" : filter == "fittings"}'>
                                <v-flex shrink><img aspect-ratio=1.0 class='filterIcon' contain :src='require("@/assets/fittings.svg")'/> </v-flex>
                                <v-flex shrink >Fittings </v-flex>
                            </v-layout>
                        </v-flex>
                        <v-flex grow class='' px-1 my-1>
                            <v-layout column wrap align-center justify-center style='' @click='filter="valves"' :class='{"filterButton" : true,"fActive" : filter == "valves"}'>
                                <v-flex shrink><v-img aspect-ratio=1.0 class='filterIcon' :src='require("@/assets/valves.svg")'> </v-img> </v-flex>
                                <v-flex shrink >Valves </v-flex>
                            </v-layout>
                        </v-flex>
                        <v-flex grow class='' px-1 my-1>
                            <v-layout column wrap align-center justify-center style='' @click='filter="accessories"' :class='{"filterButton" : true,"fActive" : filter == "accessories"}'>
                                <v-flex shrink><v-img aspect-ratio=1.0 class='filterIcon' :src='require("@/assets/accessory.svg")'> </v-img> </v-flex>
                                <v-flex shrink >Accessories </v-flex>
                            </v-layout>
                        </v-flex>


                    </v-layout> 

                </v-container>
            </v-flex >
        <!-- Categories panel -->
            <v-flex xs12 sm12 md11>
                <div >
                    <transition-group name='flip' tag='div' class='layout row wrap align-center ' >
                        <v-flex xs6 sm6 md4 lg3 v-for='(item,k) in filteredData'
                            
                            ref='cardParent'
                            :key='k+0'
                            style='padding: 0px'
                            shrink

                            :class='{"cardParent" : true,
                                    "px-3": item.count == 0 ? true : false,
                                    "disabled" : item.count == 0}'

                            
                            

                        > 
                            
                            <v-card	
                                
                            
                                hover   
                                append
                                :to='{
                                    "name": "product-lst",
                                    "params": {
                                        "id": item.id.toString(),
                                        "slug" : kebabCase(item.name)
                                } }'
                                
                                class='categoryCard'
                                
                                elevation=0
                                style='border: 1px solid rgba(0,0,0,.1)'

                                :disabled='item.count == 0'
                                

                                

                            >   
                                <div 
                                    v-if='item.count == 0'
                                    class='c-title text--center body-1 '
                                    style='color: #bff2e6 !important;'    

                                >
                                    <v-layout row justify-center>
                                        No Product Available
                                    </v-layout>
                                </div> 
                                
                                <v-img
                                    ::lzy-src="$host+'/'+item.image"
                                    :src="$host+'/'+item.image+'?lazy'"
                                    aspect-ratio="1.5"
                                    contain
                                    class='categoryImg'
                                    style='max-height: 22vh'
                                    
                                ></v-img>

                                <v-card-title>
                                    <div class='titleParent' style='z-index: 10;'>
                                        <h3 class="  categoryText" style=''>{{item.name}} </h3>
                                    </div>
                                </v-card-title>

                            </v-card>
                    </v-flex> 
                    </transition-group>

                </div>
            </v-flex>

        </v-layout>
    </v-container>

   

  <!--  </v-layout> -->


</template>

<script>

import kebabCase from 'lodash/kebabCase'


export default {

    mounted(){
        
        this.$axios.get('subcategory')
        .then( (r) => {
            if(r && r.status == 200)
            {
                //alert(JSON.stringify(r.data.results))
                this.data = r.data.results
            }
        } )


        // largest item logic
        this.$nextTick( () => {
            let cardParents = this.$refs.cardParents //document.getElementsByClassName('cardParent')
            let highestCard = 0;

            console.log('lenght is '+ cardParents.length)
            if(cardParents.length > 0)
            {
            
                for(k of cardParents){

                    console.log('before '+k)
                    if(k)
                    {
                        console.log(k)
                        if(k.offsetHeight > highestCard)
                            highestCard = k.offsetHeight
                    }
                } 
            }


            if(highestCard > 0)
            {
                for(k of cardParents){
                    k.style.height = highestCard + 'px'
                } 
            }
        } )
        

    },

    methods:{
        kebabCase : kebabCase
    },
    
    computed:{
        filteredData(){
            return this.filter == 'all'? this.data :  this.data.filter((k) => k.category == this.filter)
        }
    },
    

    data(){
        return {

            filter: 'all',
            layoutAttr: {
                style: {
                    minHeight: this.$vuetify.breakpoint.mdAndUp ? '100vh' : false,
                    width: this.$vuetify.breakpoint.smAndDown ? '100%' : false

                    
                }
            } ,
            data: []
        }
    },
    
}
</script>

<style>

    .item {
        
    }

    .list-enter-active, .list-leave-active {
    transition: all 1s;
    }
    .list-enter, .list-leave-to /* .list-leave-active for <2.1.8 */ {
    opacity: 0;
    transform: translateY(30px);
    }

    .list-move{
        transition: all 1s;
    }

    .flip-enter, .flip-leave-to{
        opacity: 0;
    }

    .flip-move{
        transition: all 1s;
    }

    .cell:nth-child(4n) {
    margin-right: 0;
    }
    .cell:nth-child(27n) {
    margin-bottom: 0;
    }
    .filterButton{
        transition: all .3s;
        filter:invert(.4);
    }

    .fActive{
        filter: invert(0);
        transform: scale(1.2);
    }

    .filterButton:hover{
        cursor: pointer;
        filter:invert(0);
        transform: scale(1.1);
    }

    .categoryText{
        font-size: 1.2rem !important;
        letter-spacing: 1px !important;
        font-weight: 400 !important;

        text-overflow: ellipsis !important;
    }
   
    @media all,(min-width: 1px) and (max-width: 960px) 
    {
        .categoryText{
            font-size: 0.85rem !important;
            font-weight: 450 !important;
        }
    }


    .filterIcon{
        width: 10vw;
        max-width: 48px;
    }


    .cardParent { flex: 0 0 100% !important; }

    .c-title{
                     
        position: absolute;

        top: 1em;
        width: calc(100% + 2em);
        /*padding: 0.8em 0; */
        left: -1.0em;
        background: #008364;
        text-transform: uppercase;
        letter-spacing: 2px !important;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        z-index: 150;
        font-size: 1.9em;
        font-weight: 500;
        
}

.c-title:before {
        content: ' ';
        position: absolute;
        top: 100%;
        left: 0px;
        height: inherit;
        border-style: solid;
        border-width: 0 1em 1em 0;
        border-color: transparent;
        border-right-color: #024d3b;
        z-index: 100;
        
        
} 
.c-title:after {
        content: ' ';
        position: absolute;
        top: 100%;
        right: 0%;
        height: inherit;
        border-style: solid;
        border-width: 0 0 1em 1em;
        border-color: transparent;
        border-left-color: #024d3b;
        z-index: 100;
        
        
} 

.disabled{
    width: 100%;
    height: 100%;
    filter: grayscale(0.8) blur(0.7px);

}


.titleParent{

    text-overflow: ellipsis;
    word-wrap: nowrap;
    overflow: hidden;
}


</style>
